@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Comfortaa:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.diaInput{z-index:1;margin:1rem;margin-top:0;display:flex}.diaInput input{background:rgba(21,0,23,0.9);border:4px solid white;padding:0.25rem;padding-left:2rem;border-radius:2px;flex-grow:1;font-size:1.25rem;color:white}.diaInput input:focus-visible{outline:none}.diaInput button{transition:125ms;aspect-ratio:1/1;padding:0.75em;border-radius:4px;font-size:2rem;line-height:1rem;background-color:white;border:0;color:#150017;font-weight:bold;font-family:monospace;margin-left:0.5rem}.diaInput button:hover{background-color:#ff0073;box-shadow:0 0 16px rgba(255,0,115,0.75);color:white}.diaInput button:active{background-color:#ad145b}

.diaInput,.dialog{width:60vw;max-width:700px;min-width:-webkit-min-content;min-width:min-content;align-self:center}.dialog{display:flex;flex-direction:column;box-sizing:border-box;height:30vh;z-index:2;background:linear-gradient(rgba(21,0,23,0.7) 0%, rgba(21,0,23,0.9) 100%);margin:1rem;align-items:flex-start;border:4px solid white;border-radius:4px;overflow-y:scroll;scrollbar-width:none;-ms-overflow-style:none;scroll-behavior:smooth}.dialog .dialogEntry{border-radius:4px;border-top-right-radius:0;max-width:70%;height:-webkit-min-content;height:min-content;position:relative;margin-top:1rem}.dialog .dialogEntry:last-child{margin-bottom:2rem}.dialog .dialogEntry .character_name{width:-webkit-min-content;width:min-content;margin:0;-webkit-filter:drop-shadow(0 0 4px currentColor);filter:drop-shadow(0 0 4px currentColor);padding:0 2rem;padding-bottom:0;background-color:transparent;border-radius:4px;border-bottom-left-radius:0;border-bottom-right-radius:0;position:relative}.dialog .dialogEntry .character_name::after{display:block;left:0;right:0;top:4px;bottom:-4px;content:"";position:absolute;z-index:-1}.dialog .dialogEntry .text{padding:2rem 2rem;padding-top:0;padding-bottom:0;margin-top:1em;border-radius:4px;border-top-left-radius:0}.dialog .dialogEntry p{color:#fff;margin:0}.dialog .dialogEntry.Waifu{align-self:flex-end}.dialog .dialogEntry.Waifu .text{background-color:rgba(0,0,0,0)}

.character{position:absolute;height:90vh}.character.left{align-self:start;margin-left:4rem}.character.right{align-self:end;margin-right:4rem}.character.center{align-self:center}

h1,h2,h3,h4,h5,h6{font-weight:700}body,h1,h2,h3,h4,h5,h6,p,span,input,button{font-family:"Comfortaa", cursive}

.novelWrapper{position:relative;height:100vh;display:flex;flex-direction:column;align-items:stretch;justify-content:flex-end;overflow:hidden}.novelWrapper .bcg{position:absolute;top:0;left:0;z-index:0;width:100vw;height:100vh;object-fit:cover}.novelWrapper .characters{position:absolute;left:0;right:0;top:0;bottom:0;z-index:1;display:flex;justify-content:center;flex-direction:column}

